@import url('https://fonts.googleapis.com/css?family=Numans');

html,body{
height: 100%;
font-family: 'Numans', sans-serif;
}
.register{
    background-image: url('./img/auth.svg');
    background-size: 30%;
    background-position: left;
    background-repeat: no-repeat;
}
.login{
    background-image: url('./img/auth.svg');
    background-size: 30%;
    background-position: left;
    background-repeat: no-repeat;
}

.container{
height: 90%;
align-content: center;
}
a.disabled {
    pointer-events: none;
    cursor: default;
}
.custom-card1{
height: 390px;
margin-top: auto;
margin-bottom: auto;
width: 400px;
background-color: rgba(0,0,0,0.5) !important;
}

.social_icon span{
font-size: 60px;
margin-left: 10px;
color: #f6b21b;
}

.social_icon span:hover{
color:  white;
cursor: pointer;
}

.card-header h3{
color: white;
}

.social_icon{
position: absolute;
right: 20px;
top: -45px;
}

.input-group-prepend span{
width: 50px;
background-color: #f6b21b;
color: black;
border:0 !important;
}

input:focus{
outline: 0 0 0 0  !important;
box-shadow: 0 0 0 0 !important;

}

.remember{
color: white;
}

.remember input
{
width: 20px;
height: 20px;
margin-left: 15px;
margin-right: 5px;
}

.login_btn{
color: black;
background-color: #f6b21b;
width: 100px;
}

.login_btn:hover{
color: black;
background-color: white;
}

.links{
color:black;
}

.links a{
margin-left: 4px;
color: #f6b21b;
}
